@import 'variables';
body{
    font-family: $textFont;
}


/* dIMdMI TEMPLATES logo*/
.logo-container{
    display: block;
}
.logo {
    color: #1b91ff;
    font-family: "AG Book Pro";
    font-size: 40px;
    font-weight: 700;
}

.logo-span {
    font-size: 25px;
}

.logo-span--I {
    font-size: 32px;
}

.logo-name {
    color: #7f7f7f;
    font-family: "AG Book Pro";
    font-size: 9px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 9.4px;
}

